import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'prettyHousekeepingAssignments'
})
export class     PrettyHousekeepingAssignments implements PipeTransform {

  transform(value: any, args?: any): any {
      console.log(value)
      console.log(args)
      if(typeof value == 'string'){
        let x = value.replace(/_/g, ' ');
        console.log(x)
        return x
      }
      else{
          return false
      }
  }

}
